import * as Yup from "yup";
import { useState, useEffect } from "react";
// form
import { useForm } from "react-hook-form";
import { useLocation } from "react-router-dom";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import {
  Stack,
  IconButton,
  InputAdornment,
  Alert,
  Grid,
  Box,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
// hooks
import useAuth from "../../../hooks/useAuth";
import useIsMountedRef from "../../../hooks/useIsMountedRef";
// components
import Iconify from "../../../components/Iconify";
import {
  FormProvider,
  RHFTextField,
  RHFPhoneField,
  RHFSelect,
} from "../../../components/hook-form";
// ----------------------------------------------------------------------

const LANGUE_OPTION = [
  { label: "Français", value: "fr" },
  { label: "Créole Haïtien", value: "ht" },
  { label: "English", value: "en" },
];

export default function RegisterForm() {
  const { register } = useAuth();
  const location = useLocation();
  const refCode = new URLSearchParams(location.search).get("ref");

  const isMountedRef = useIsMountedRef();

  const [showPassword, setShowPassword] = useState(false);

  const RegisterSchema = Yup.object().shape({
    ref_code: Yup.string().nullable(),
    phone: Yup.string().required("A phone number is required"),
    langue: Yup.string().required("Langue is required"),
    firstName: Yup.string().required("First name is required"),
    lastName: Yup.string().required("Last name is required"),
    username: Yup.string()
      .required("Username is required")
      .matches(/^[A-Za-z0-9_]{1,15}$/, "your username is incorect"),
    email: Yup.string()
      .email("Email must be a valid email address")
      .required("Email is required"),
    password: Yup.string().required("Password is required"),
    passwordConfirmation: Yup.string()
      .oneOf([Yup.ref("password"), null], "Passwords must match")
      .required("Password is required"),
  });

  const defaultValues = {
    phone: "",
    langue: LANGUE_OPTION[0].value,
    firstName: "",
    lastName: "",
    username: "",
    email: "",
    password: "",
    passwordConfirmation: "",
    ref_code: refCode,
  };

  const methods = useForm({
    resolver: yupResolver(RegisterSchema),
    defaultValues,
  });

  const {
    watch,
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const values = watch();

  const onSubmit = async (data) => {
    try {
      await register(
        data.phone,
        data.langue,
        data.username,
        data.email,
        data.password,
        data.firstName,
        data.lastName,
        data.ref_code
      );
      window.location.reload();
    } catch (error) {
      // console.error(error.response.data);
      // reset();
      if (isMountedRef.current) {
        window.scrollTo(0, 0);
        setError("afterSubmit", {
          ...error,
          message: Object.values(error?.response?.data).flat().join(" "),
        });
        if (error.response.data.phone) {
          setError("phone", { ...error, message: error.response.data.phone });
        }
        if (error.response.data.username) {
          setError("username", {
            ...error,
            message: error.response.data.username,
          });
        }
        if (error.response.data.ref_code) {
          setError("ref_code", {
            ...error,
            message: error.response.data.ref_code,
          });
        }
        if (error.response.data.email) {
          setError("email", { ...error, message: error.response.data.email });
        } else if (error.response.data.non_field_errors) {
          setError("email", {
            ...error,
            message: error.response.data.non_field_errors,
          });
        }
        if (error.response.data.password) {
          setError("password", {
            ...error,
            message: error.response.data.password,
          });
          setError("passwordConfirmation", {
            ...error,
            message: error.response.data.password,
          });
        }
      }
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Stack spacing={3}>
        {!!errors.afterSubmit && (
          <Alert severity="error">{errors.afterSubmit.message}</Alert>
        )}
        <Stack direction={{ xs: "column", sm: "row" }} spacing={2}>
          <RHFPhoneField type="tel" name="phone" label="Phone" />
          {/* <RHFTextField
            type="tel" name="phone" label="Phone"
            placeholder="Your phone number"
            InputLabelProps={{ shrink: true }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">+</InputAdornment>
              ),
              type: "number",
            }}
          /> */}
          <RHFSelect name="langue" label="Langue" placeholder="Langue">
            <option value="" />
            {LANGUE_OPTION.map((option) => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </RHFSelect>
        </Stack>

        <Stack direction={{ xs: "column", sm: "row" }} spacing={2}>
          <RHFTextField
            name="firstName"
            label={
              values?.langue === "en"
                ? "First name"
                : values?.langue === "ht"
                ? "Nonw"
                : "Prénom"
            }
          />
          <RHFTextField
            name="lastName"
            label={
              values?.langue === "en"
                ? "Last name"
                : values?.langue === "ht"
                ? "Siyati"
                : "Nom"
            }
          />
        </Stack>
        <Stack direction={{ xs: "column", sm: "row" }} spacing={2}>
          <RHFTextField
            name="username"
            label={
              values?.langue === "en"
                ? "Username"
                : values?.langue === "ht"
                ? "Ti nom"
                : "Pseudonyme"
            }
          />
          <RHFTextField
            type="email"
            name="email"
            label={
              values?.langue === "en"
                ? "Email address"
                : values?.langue === "ht"
                ? "Adrès mel"
                : "Adresse e-mail"
            }
          />
        </Stack>

        <Stack direction={{ xs: "column", sm: "row" }} spacing={2}>
          <RHFTextField
            name="password"
            label={
              values?.langue === "en"
                ? "Password"
                : values?.langue === "ht"
                ? "Modpas"
                : "Mot de passe"
            }
            type={showPassword ? "text" : "password"}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    edge="end"
                    onClick={() => setShowPassword(!showPassword)}
                  >
                    <Iconify
                      icon={showPassword ? "eva:eye-fill" : "eva:eye-off-fill"}
                    />
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />

          <RHFTextField
            name="passwordConfirmation"
            label={
              values?.langue === "en"
                ? "Comfirm Password"
                : values?.langue === "ht"
                ? "Konfime modpas la"
                : "Confirmez le mot de passe"
            }
            type={showPassword ? "text" : "password"}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    edge="end"
                    onClick={() => setShowPassword(!showPassword)}
                  >
                    <Iconify
                      icon={showPassword ? "eva:eye-fill" : "eva:eye-off-fill"}
                    />
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
        </Stack>

        <Stack spacing={2}>
          <RHFTextField
            name="ref_code"
            label={
              values?.langue === "en"
                ? "Referral code (not required)"
                : values?.langue === "ht"
                ? "kòd referans (si genyen)"
                : "Code de Parrainage (facultatif)"
            }
            helperText={
              values?.langue === "en"
                ? "*Optional"
                : values?.langue === "ht"
                ? "*Li pa Obligatwa"
                : "*Non Obligatoire"
            }
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Iconify
                    color="#ff8612"
                    icon={"octicon:gift-16"}
                    width={24}
                    height={24}
                  />
                </InputAdornment>
              ),
            }}
          />
        </Stack>

        <LoadingButton
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          loading={isSubmitting}
        >
          {values?.langue === "en"
            ? "Register"
            : values?.langue === "ht"
            ? "Enskri"
            : "Enregistrer"}
        </LoadingButton>
      </Stack>
    </FormProvider>
  );
}
